.section{
    width: 100%;
    position: relative;
    overflow: hidden;
    padding: 0 0 50px;
    margin: 0 auto 30px;
    display: flex;
    flex-direction: column;
}

.h2{
    font-size: 6vw;
    margin: 0 0 20px;
    max-width: 1366px;
}

.contentTill{
    max-width: 1366px;
}

.newClassAdded{
    width: 100%;
}

@media (max-width:767px){
    .h2{
        font-size: 9vw;
        margin: 20px 0;
    }
}

.svgWrap{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    left: 0;
}

.newClassAdded .svg-animate {
    width: 50%;
    transform: var(--leftSvg);
    opacity: 0;
}

.newClassAdded .svg-animate{
    opacity: 1;
}

.newClassAdded .svg-animate g path{
    stroke-dashoffset: 88;
    stroke-dasharray: 88;
    stroke-width: 0;
    opacity: 1;
}
.newClassAdded .svg-animate g:nth-child(1) path{
    animation: svgAnimation .3s ease-out 0.25s forwards;
}

.newClassAdded .svg-animate g:nth-child(2) path{
    animation: svgAnimation .3s ease-out 0.45s forwards;
}

.newClassAdded .svg-animate g:nth-child(3) path{
    animation: svgAnimation .3s ease-out 0.65s forwards;
}

@keyframes svgAnimation{
    from{
        stroke-dashoffset: 88;
        stroke-width: 0;
    }
    to{
        stroke-dashoffset: 0;
        stroke-width: 1.5;
    }
}

@media (max-width:767px){
    .svg-animate{
        transform: var(--mobleft);
    }
}
